import {_decorator, Component, MeshRenderer, Node, Size, Vec4} from 'cc';

const {ccclass, property} = _decorator;

@ccclass('Game_Board')
export class Game_Board extends Component {
  @property(Size) grid_size: Size;
  @property(MeshRenderer) renderable: MeshRenderer;

  start() {
    this.renderable.material.setProperty(
        'tilingOffset',
        new Vec4(this.grid_size.width, this.grid_size.height, 0, 0));
  }

  update(deltaTime: number) {}
}
